import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from '../shared/guards/auth.guard';
import { FetchTeamsComponent, EditTeamsComponent, AddTeamComponent, ProfileComponent } from './';

const esportRoutes: Routes = [
  {
    path: '',
    redirectTo: 'all-teams',
    pathMatch: 'full'
  },
  {
    path: 'all-teams',
    component: FetchTeamsComponent,
  },
  {
    path: 'registration',
    component: AddTeamComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'profile',
    component: ProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'edit-teams',
    component: EditTeamsComponent,
    canActivate: [AuthGuard]
  }
]

@NgModule({
  imports: [RouterModule.forChild(esportRoutes)],
  exports: [RouterModule]
})

export class EsportRoutingModule { }