import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { HttpEventType, HttpEvent } from '@angular/common/http';
import { Router } from '@angular/router';
import { IsLoadingService } from '@service-work/is-loading';
import { Observable } from 'rxjs';
import {
  EsportService,
  NotificationsService,
  elementAnimations
} from '../../shared';

@Component({
  selector: 'app-teams-add',
  templateUrl: './add-team.component.html',
  styleUrls: ['./add-team.component.scss'],
  animations: [elementAnimations]
  // changeDetection: ChangeDetectionStrategy.OnPush
})

export class AddTeamComponent implements OnInit{
	preview: string;
  form: FormGroup;
  percentDone: any = 0;

	constructor(
    public router: Router,
    public esportService: EsportService,
    private isLoadingService: IsLoadingService,
    public notification: NotificationsService
  ) {

    this.form = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.maxLength(30)]),
      contact: new FormControl('', [Validators.required, Validators.minLength(10), Validators.maxLength(13)]),
      address: new FormControl('', [Validators.required, Validators.maxLength(30)]),
      logo: new FormControl(null, [Validators.required])
    })
  }

	ngOnInit() {
  }

  onFileSelected(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({
      logo: file
    });
		this.form.get('logo').updateValueAndValidity()

		// File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.preview = reader.result as string;
    }
    reader.readAsDataURL(file)
  }

	submitForm() {
    this.isLoadingService.add();
    this.form.disable();
		this.esportService.addTeam(
			this.form.value.name,
			this.form.value.contact,
			this.form.value.address,
			this.form.value.logo
		)
		.subscribe((event: HttpEvent<any>) => {
      switch (event.type) {
        case HttpEventType.Sent:
          console.log("Request has been made!");
          break;
        case HttpEventType.ResponseHeader:
          console.log("Response header has been received!");
          break;
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round((event.loaded / event.total) * 100);
          console.log(`Uploaded! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          if (event.body.success === false) {
            this.notification.error(event.body.message);
          } else if (event.body.success === true) {
            this.percentDone = false;
            this.notification.success(event.body.message);
            this.router.navigate(["esport/all-teams"]);
            this.isLoadingService.remove();
          }
      }
    })
  }

}
