import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatSlideToggle, MatDialog } from '@angular/material';
import { BreakpointObserver, Breakpoints } from "@angular/cdk/layout";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";

import { IsLoadingService } from '@service-work/is-loading';
import { EsportService, AuthService, elementAnimations, NotificationsService } from '../../shared';
import { DialogBoxComponent } from '../dialog-box/dialog-box.component';

@Component({
  selector: 'app-edit-teams',
  templateUrl: './edit-teams.component.html',
  styleUrls: ['./edit-teams.component.scss'],
  animations: [elementAnimations]
})
export class EditTeamsComponent implements OnInit {
  isVerified: boolean;
  allTeams: any = [];
  singleTeam: any;

  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(map(result => result.matches));

  DATA_SOURCE: any = [];
  displayedColumns = ['isVerified', 'name', 'contact', 'address', 'score', 'logo', 'action'];

  constructor(
    private breakpointObserver: BreakpointObserver,
    private esportService: EsportService,
    private authService: AuthService,
    private isLoadingService: IsLoadingService,
    public notification: NotificationsService,
    public dialog: MatDialog
  ) { }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  @Input () checked: boolean;
 
  ngOnInit() {
    // GET all teams onInit
    this.authService.loadToken();
    this.esportService.getTeams().subscribe(res => {
      this.allTeams = res['teams'];

      this.DATA_SOURCE = new MatTableDataSource(this.allTeams);
      this.DATA_SOURCE.paginator = this.paginator;
      this.DATA_SOURCE.sort = this.sort;
    });

    // APPLY loader OnInit service
    this.isLoadingService.add(this.esportService.getTeams());
  }

  // FILTER on (keyup)
  applyFilter(filterValue: string) {
    this.DATA_SOURCE.filter = filterValue.trim().toLowerCase();
  }

  // PUT newScore
  editScore(team) {
    const dialogRef = this.dialog.open(DialogBoxComponent, {
      width: '400px',
      height: '400px',
      data: team,
      disableClose: true
    });
  
    dialogRef.afterClosed().subscribe(result => {
      this.isLoadingService.add(
        this.esportService.addScore(team).subscribe((data) => {
          if (data.success === false) {
            this.notification.error(data.message);
          } else if ( data.success === true) {
            this.notification.success(data.message);
          }
        })
      ) // end loading
    })
  }

  // TOGGLE to verify
  onToggle(team, event) {
    team.isVerified = !team.isVerified;
   
    this.isLoadingService.add(
      this.esportService.verifyTeam(team).subscribe((data) => {
        console.log(data);
        this.notification.success(data.message);
      })
    ); // End of isLoading$
  }

}