import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { BreakpointObserver, Breakpoints } from "@angular/cdk/layout";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";

import { IsLoadingService } from '@service-work/is-loading';
import { EsportService, AuthService, elementAnimations } from '../../shared';

@Component({
  selector: 'app-fetch-teams',
  templateUrl: './fetch-teams.component.html',
  styleUrls: ['./fetch-teams.component.scss'],
  animations: [elementAnimations]
})
export class FetchTeamsComponent implements OnInit {

  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(map(result => result.matches));

  DATA_SOURCE: any = [];
  displayedColumns = ['isVerified', 'name', 'address', 'score', 'logo'];

  constructor(
    private breakpointObserver: BreakpointObserver,
    private esportService: EsportService,
    private authService: AuthService,
    private isLoadingService: IsLoadingService
  ) { }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;
 
  ngOnInit() {
    // GET all teams onInit
    this.authService.loadToken();
    this.esportService.getTeams().subscribe(res => {
      let teamsList = res['teams'];

      this.DATA_SOURCE = new MatTableDataSource(teamsList);
      this.DATA_SOURCE.paginator = this.paginator;
      this.DATA_SOURCE.sort = this.sort;
    });

    // apply loader OnInit service
    this.isLoadingService.add(this.esportService.getTeams());
  }

  // Filtering on (keyup)
  applyFilter(filterValue: string) {
    this.DATA_SOURCE.filter = filterValue.trim().toLowerCase();
  }

}